.form {
  padding-top: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  label {
    margin-bottom: 20px;
    padding: 0 15px;
    span {
      // font-weight: bold;
      margin-bottom: 5px;
      display: inline-block;
    }
  }
  .btn-container {
    text-align: right;
    width: 100%;
    padding: 0 15px;
    button {
      font-size: 18px;
      width: 150px;
      height: 48px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      @media (max-width: 767px) {
        width: 100%;
      }
    }
  }
}
